body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__minutes {
  animation: 3600s linear infinite rotate-minutes;
  position: absolute;
}

.stopwatch__minutes--visible {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
}

.stopwatch__minutes--invisible {
  width: 3vmin;
  height: 20vmin;
}

.stopwatch__minutes--speed-up {
  animation: 600s linear infinite rotate-minutes;
}

.stopwatch__seconds {
  animation: 60s linear infinite rotate-seconds;
  position: absolute;
}

.stopwatch__seconds--visible {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
}

.stopwatch__seconds--invisible {
  width: 1.5vmin;
  height: 38vmin;
}

.stopwatch__seconds--speed-up {
  animation: 10s linear infinite rotate-minutes;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes rotate-seconds {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate-minutes {
  100% {
    transform: rotate(360deg);
  }
}
